import React from "react";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import TableBody from "@material-ui/core/TableBody";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";

export default function MagicVar({ isFile, open, onClose, isSlave }) {
    return (
        <Dialog
            open={open}
            onClose={onClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {isFile ? "" : ""}
            </DialogTitle>
            <DialogContent>
                <TableContainer>
                    <Table size="small" aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{randomkey16}"}
                                </TableCell>
                                <TableCell>16</TableCell>
                                <TableCell>N6IimT5XZP324ACK</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{randomkey8}"}
                                </TableCell>
                                <TableCell>8</TableCell>
                                <TableCell>gWz78q30</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{timestamp}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>1582692933</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{timestamp_nano}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>1582692933231834600</TableCell>
                            </TableRow>
                            {!isSlave && (
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"{uid}"}
                                    </TableCell>
                                    <TableCell>ID</TableCell>
                                    <TableCell>1</TableCell>
                                </TableRow>
                            )}
                            {isFile && (
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"{originname}"}
                                    </TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>MyPico.mp4</TableCell>
                                </TableRow>
                            )}
                            {!isFile && !isSlave && (
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"{path}"}
                                    </TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>///</TableCell>
                                </TableRow>
                            )}
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{date}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>20060102</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{datetime}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>20060102150405</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{year}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>2006</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{month}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>01</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{day}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>02</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{hour}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>15</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{minute}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>04</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">
                                    {"{second}"}
                                </TableCell>
                                <TableCell></TableCell>
                                <TableCell>05</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    
                </Button>
            </DialogActions>
        </Dialog>
    );
}
